@import url(https://fonts.googleapis.com/css?family=Roboto);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.introContainer {
    position: relative;
    height: 50vw;
}

.introImage {
  width: 100%; /* width of container */
  height: 100%; /* height of container */
  object-fit: cover;
}

.portraitImage {
  position: absolute;
  top: 20%;
  right: 15%;
  width: 25%;

}

.introText {
    position: absolute;
    top: 5%;
    left: 5%;
    color: black;
  }

.introTextHello {
    color: black;
    font-size: 6vw;
  }

.introTextName {
    color: black;
    font-size: 6vw;
  }

.introTextTitle {
  color: black;
  font-size: 3vw;
}


body {
  font-family: 'Roboto';font-size: 22px;
}
.aboutMeTitle {
    color: black;
    padding: 5%;
    font-size: 6vw;
}

.aboutMeContent {
    padding: 5%;
}

.aboutMeImage {
    width: 50%;
    display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10%;
}
.projectsTitle {
    color: white;
    padding: 5%;
    font-size: 6vw;
}

.projectsContent {
    padding: 5%;
}

.projectsContainer {
    background-color: #ccc5b5;
}
.contentPortrait {
    display: block;
    margin-left: auto;
    margin-right: auto;
    white-space: pre;
    width: 60%;
}

.contentLandscape {
    display: block;
    margin-left: auto;
    margin-right: auto;
    white-space: pre;
    width: 100%;
    padding: 5%;
}

.contentMobile {
    display: block;
    margin-left: auto;
    margin-right: auto;
    white-space: pre;
    width: 100%;
    padding: 5%;
}



.contactsContainer {
    background-color: black;
    border-style: solid;
    border-color: black;
    border-width: 1.5vw;
}

.contactsText {
    color: white;
    font-size: 15px;
    text-align: center;
    border-top: 5vw solid black;
}
